export default {
	data() {
		return {
			tab: [{
					name: '公司介绍',
					id: 0,
				},
				{
					name: '公司资质',
					id: 1,
				},
			],
			tabid: 0,
			showIntro: true,
			showAble: false,
			platform: '',
		};
	},
	onLoad() {
		this.getPlatform();
	},
	onShow() {
		this.getPlatform();
	},
	methods: {
		changeTab(id) {
			this.tabid = id;
			if (this.tabid == 0) {
				this.showIntro = true;
				this.showAble = false;
			} else if (this.tabid == 1) {
				this.showIntro = false;
				this.showAble = true;
			}
		},

		//获取平台信息
		async getPlatform() {
			var res = await this.$request.get('/platform', );
			// console.log(res);
			this.platform = res.data.result[0];
		},
	}
}
